import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { environment } from '@env/environment';

@Injectable({
  providedIn: 'root'
})
export class BotRequestService {
  private baseUrl = environment.apiUrl +'bot_requests';

  constructor(private http: HttpClient) { }

  createRequest(botrequest: Object): Observable<Object> {
    return this.http.post(`${this.baseUrl}/create_rider/`, botrequest);
  }

  getRequestsList(id: number): Observable<any> {
    return this.http.get(`${this.baseUrl}/request_list/${id}`); 
  } //user id

  getAllRequestsList(id: number): Observable<any> {
    return this.http.get(`${this.baseUrl}/all_request_list/${id}`);
  }

  runRequest(botrequest: Object): Observable<Object> {
    return this.http.post(`${this.baseUrl}/run/`, botrequest);
  }
  
  deleteRequest(id: number): Observable<Object> {
    return this.http.delete(`${this.baseUrl}/delete/${id}`);
  }

}
