import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { User} from '@app/models/user';

import { environment } from '@env/environment';

@Injectable({ providedIn: 'root' })

export class AuthenticationService {
    private baseUrl = environment.apiUrl +'users';

    private currentUserSubject: BehaviorSubject<User>;
    public currentUser: Observable<User>;
    private httpOptions: any;
   // public token: string;
  //  public token_expires: Date;

    constructor(private http: HttpClient) {
        this.currentUserSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('currentUser')));
        this.currentUser = this.currentUserSubject.asObservable();
    }

    public get currentUserValue(): User {
        return this.currentUserSubject.value;
    }

    login(username, password) {
        return this.http.post<any>(`${this.baseUrl}/auth/jwt/create/`, { username, password })
            .pipe(map(user => {
                // store user details and jwt token in local storage to keep user logged in between page refreshes
                localStorage.setItem('currentUser', JSON.stringify(user)); //not ideal as both token as saved at same place
                this.currentUserSubject.next(user);
                return user;
            }));
    }

    logout() {
        // remove user from local storage and set current user to null
        this.http.post(`${this.baseUrl}/logout/`, this.currentUser);
        localStorage.removeItem('currentUser');
        this.currentUserSubject.next(null);
    }

}
