import { Component, OnInit, Input } from '@angular/core';
import { BotRequest} from '../models/bot-request';
import { BotRequestService} from '@ser/bot-request.service';
import { AllRequestListComponent } from '@app/all-request-list/all-request-list.component';

@Component({
  selector: 'app-all-request-details',
  templateUrl: './all-request-details.component.html',
  styleUrls: ['./all-request-details.component.css']
})
export class AllRequestDetailsComponent implements OnInit {
  
  @Input() botrequest: BotRequest;
  running = false;

  constructor(private botRequestService: BotRequestService, private listComponent: AllRequestListComponent) { }

 ngOnInit() {
  } 

  run() {
    this.botRequestService.runRequest(this.botrequest)
      .subscribe(
        data => {
          console.log(data);
          this.running=true;
          this.listComponent.reloadData();
        },
        error => console.log(error));
  }
  
  delete() {
    this.botRequestService.deleteRequest(this.botrequest.id)
      .subscribe(
        data => {
          console.log(data);
          this.listComponent.reloadData();
        },
        error => console.log(error));
  }
}

