import { Component, OnInit, Input } from '@angular/core';
import { BotRequest} from '../models/bot-request';
import { BotRequestService} from '@ser/bot-request.service';
import { RequestListComponent } from '@app/request-list/request-list.component';

@Component({
  selector: 'app-request-details',
  templateUrl: './request-details.component.html',
  styleUrls: ['./request-details.component.css']
})
export class RequestDetailsComponent implements OnInit {
  
  @Input() botrequest: BotRequest;
  running = false;

  constructor(private botRequestService: BotRequestService, private listComponent: RequestListComponent) { }

 ngOnInit() {
  } 
 
  delete() {
    this.botRequestService.deleteRequest(this.botrequest.id)
      .subscribe(
        data => {
          console.log(data);
          this.listComponent.reloadData();
        },
        error => console.log(error));
  }
}

