import { Component, OnInit } from '@angular/core';
import { BotRequest} from '../models/bot-request';
import { BotRequestService} from '@ser/bot-request.service';
import { Observable } from 'rxjs';

import {AuthenticationService } from '@ser/authentication.service';
import { User} from '@app/models/user';

@Component({
  selector: 'app-request-list',
  templateUrl: './request-list.component.html',
  styleUrls: ['./request-list.component.css']
})
export class RequestListComponent implements OnInit {
  currentUser: User;
  botrequests: Observable<BotRequest[]>;

   constructor(private botRequestService: BotRequestService,
               private authenticationService: AuthenticationService,
   ) {
   this.authenticationService.currentUser.subscribe(x => this.currentUser = x);
    }

  ngOnInit() {
    this.reloadData();
  }

  reloadData() {
    this.botrequests = this.botRequestService.getRequestsList(this.currentUser.id);
  }
}